import React, { Component } from 'react';

export default class Cell extends Component{
    constructor(props){
        super(props);

        this.state = ({
        });
    }

    clickHandle(x,y){
        let newArray = this.props.map.source;

        newArray[x][y] = 0;
        this.props.mapActions(newArray);
    }

    render(){
        let num = this.props.serialNumber;
        let temp = 'cell ';


        // switch (num){
        //     case 0:
        //         temp += 'cakeHalf';
        //         break;
        //     default:
        //         temp += '';
        //         break;
        // }

        temp = this.props.map.mapping[num];

        let returnedCell = (
            <div className={temp} onClick={this.clickHandle.bind(this, this.props.x, this.props.y)}>

            </div>
        );
        return returnedCell;
    }
}